// Colors
$dark-gray: #333;
$gray: #666;
$blue: #1c88bf;
$dark-blue: #364051;
$off-white: #f5f5f5;
$computer-grey: #d4d0c8;
$windows-blue: #0a246a;
$windows-teal: #008282;
$windows-blue-2: #000082;
$windows-red: #fa5252;//#f04e1f;
$windows-off-white: #fffccd;

//Font-Size

$font-size-large: 1.8rem;
$font-size-small: 1.4rem;

// Spacing
$s-size: 1.2rem;
$m-size: 1.6rem;
$l-size: 3.2rem;
$xl-size: 4.8rem;
$desktop-breakpoint: 45rem;

//background-mixin
@mixin micro-background {
    background: conic-gradient(
        from 45deg,
        #020681 0,
        #020681 90deg,
        #000337 90deg,
        #000337 180deg,
        #0818ff 180deg,
        #0818ff 270deg,
        #0f9bfe 270deg,
        #0f9bfe 360deg
      );
      background-size: 1em 1em;
      height: 100vh;
  }



@mixin undulating-background {


    width: 100%;
    height: 100vh;
 
    background-size: 300% 300%;
    background-image: linear-gradient(
          -45deg, 
          // #001f1f 0%, 
          // #004545 25%, 
          // #016161 50%, 
          // #019191 100%
          #000a0a 0%, 

          #000a0a 12.5%, 
          #002d2d 25%, 
          #016161 50%, 
          #019191 100%
          
    );  
    animation: AnimateBG 20s ease infinite;

  
  @keyframes AnimateBG { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }
  
}

@mixin basic-box-shadow {
  box-shadow:rgba(0,0,0,0.4) 2px 2px 0, white -1px -1px 0px;

}
@mixin basic-box-shadow-inset {
  box-shadow:rgba(0,0,0,0.4) 2px 2px 0  inset, white -1px -1px 0px inset;
}